import { Box , Heading, Spacer,Image, Center,Text, Spinner,Input,Button, Stack} from '@chakra-ui/react';
import React, { useState,useEffect, useRef } from 'react';
import {useParams} from 'react-router-dom';
import axios from 'axios';
import Header from './Header';


function Product(props) {
  const param =useParams();
  const id =param.id;
  // console.log(id)
  // console.log(param)
  const commentref=useRef('');
  const [Comment,setComment]=useState([]);
  const [storeProduct,setStoreProduct]=useState([]);
  const [loading,setLoading]=useState(true);
  const [input,setInput]=useState("")

  useEffect(()=>{
    axios.get(`https://fakestoreapi.com/products/${id}`).then(({data})=>{
      setLoading(false);
      setStoreProduct(data);
     
    })
  },[])
  useEffect(()=>{
    localStorage.setItem('my-list',JSON.stringify(Comment))
  })

  const StoreItems=({title,price,image,description})=>{
    return(
            <Center>
        <Box p={4} borderRadius="lg" borderWidth="1px" d="flex">
            <Image boxSize='300px' src={image}/>
            <Box ml={5} mt={10} alignItems="center">
              <Heading>$ {price}</Heading>
              <Heading size="sm" fontWeight="semibold" mt={4}>{title}</Heading>
            <Text fontSize='xl' noOfLines={2} mt={3}>{description}</Text>
            </Box>
            
            <Spacer/>
            
            {/* <Tag mt={4} bg="gray.400">$ {price}</Tag> */}
        </Box>
            </Center>
    );
};

  return (

    <Box>
        <Header title="Product Detail"/>
          {loading ? (<Center mt={10}><Spinner/></Center>)
          :
          ( <Box mt={4}>
            <StoreItems {...storeProduct}/>
            <Heading mt={4} size="md">Comment</Heading>
            <Input id='review' value={input} ref={commentref} mt={4} placeholder='Product Review' onChange={()=>{setInput(commentref.current.value);}}/>
            <Button colorScheme='blue' mt={4} onClick={()=>{
              
              setComment((oldItems)=>{
                return [...oldItems,input]
              })
              console.log(Comment)
              setInput("")
            }}>Submit</Button>
            <Stack>
              {Comment.map((com)=>{
                return(
                  <Box mt={4} p={5} borderRadius="lg" borderWidth="1px">
                  <Text>{com}</Text>
                  </Box>
                );
              })}
            </Stack>
            </Box>

          
            )
          
          }
         
        
        
    </Box>
  )
}

export default Product;