import { Box, Heading, SimpleGrid, Spacer,Tag ,Image,Input, Spinner, Center, GridItem} from '@chakra-ui/react'
import React,{ useEffect, useState} from 'react'
import Header from './Header';
import { Link } from 'react-router-dom';
import axios from 'axios';
// import Search from './Search';
const StoreIems=({title,price,image})=>{
    return(
        <Box p={4} borderRadius="lg" borderWidth="1px">
            
            <Image boxSize='150px' src={image}/>
            <Heading size="sm" fontWeight="normal">{title}</Heading>
            <Spacer/>
            
            <Tag mt={4} bg="gray.400">$ {price}</Tag>
        </Box>
    );
};
function Store() {
    // const itemNameRef = useRef("");
    // const itemPriceRef = useRef("");
    const [storeItem,setStoreItem]=useState([])
    const [loading,setLoading]=useState(true);
    const [filteredItems,setfilteredItems]=useState([]);


    useEffect(()=>{
        axios.get('https://fakestoreapi.com/products').then(({data})=>{
          setLoading(false)
          setStoreItem(data)
          setfilteredItems(data)
        })
      },[])

    // useEffect(()=>{
    //     setfilteredItems(storeItem)
    // },[storeItem])
  return (
    <Box p={4}>
        <Header title="Product Review"/>
        {loading ?
        ( 
        <Center mt={10}>
        <Spinner/>
        </Center>
        
        ):
       ( <Box p={4}>
        <Input onChange={(e)=>{
            let f = storeItem.filter((item)=>item.title.toLowerCase().includes(e.target.value.toLowerCase()));
            console.log('f',f);
           setfilteredItems(f);
        }}
        placeholder="search"
        mt={4}
        />
       
      
        <SimpleGrid columns={2} spacing={10}>
        {filteredItems.map((item) => {
                    return(
                        <GridItem>
                        <Link to={{
                            pathname:`/product/${item.id}`,
                            state:item
                            }}>
                        <StoreIems {...item}/>
                        </Link>
                       
                        
                        </GridItem>
                    );})
                }
        </SimpleGrid>

        </Box>
       )
}

       {/* <Input ref={itemNameRef} mt={10} placeholder="item name"/>
       <Input ref={itemPriceRef} mt={10} placeholder="item price"/>
       <Button onClick={()=>{
        onItemAdd({
            title:itemNameRef.current.value,
            price:itemPriceRef.current.value
        });
       }}>Add item</Button> */}
    </Box>
  )
}

export default Store